/* CSS Reset */
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  font-size: 62.5%; }

body {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  /*font-size: 16px;*/
  line-height: 1.7;
  box-sizing: border-box; }

img {
  display: block; }

.btn {
  font-family: 'Raleway', sans-serif;
  font-size: 1.4rem;
  font-weight: bolder; }
  .btn:link, .btn:visited {
    text-transform: uppercase;
    text-decoration: none;
    padding: 1.2rem 3rem;
    border-radius: 1.7rem; }
  .btn:hover {
    transform: translateY(-0.3rem); }
  .btn:active {
    transform: translateY(-0.1rem); }
  .btn--accent {
    background-color: #fd8d5c;
    color: #fff; }
  .btn--secondary {
    background-color: #a0cd3a;
    color: #fff; }

hr {
  background-color: #fff;
  margin: .5rem 0 2rem;
  width: 4rem;
  height: 0.3rem;
  border: 0; }

.donation-examples {
  width: 144rem;
  background-color: #a0cd3a;
  display: flex;
  justify-content: center; }
  .donation-examples hr {
    display: none; }
  .donation-examples > div {
    display: flex;
    justify-content: center;
    align-items: center;
    border-right: solid 0.3rem #e6ffae;
    padding: 0 2rem;
    margin: 2rem 0; }
    .donation-examples > div:nth-last-child(1) {
      border-right: none;
      background: url("../../assets/images/dog-paw.png") right no-repeat;
      margin: 0; }
  .donation-examples .money {
    color: #fff;
    font-size: 6rem;
    font-weight: 900;
    margin-right: 1rem; }
  .donation-examples .caption {
    color: #e6ffae;
    font-size: 2.5rem;
    font-weight: 900;
    line-height: 2.5rem; }
