/* CSS Reset */
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  font-size: 62.5%; }

body {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  /*font-size: 16px;*/
  line-height: 1.7;
  box-sizing: border-box; }

img {
  display: block; }

.btn {
  font-family: 'Raleway', sans-serif;
  font-size: 1.4rem;
  font-weight: bolder; }
  .btn:link, .btn:visited {
    text-transform: uppercase;
    text-decoration: none;
    padding: 1.2rem 3rem;
    border-radius: 1.7rem; }
  .btn:hover {
    transform: translateY(-0.3rem); }
  .btn:active {
    transform: translateY(-0.1rem); }
  .btn--accent {
    background-color: #fd8d5c;
    color: #fff; }
  .btn--secondary {
    background-color: #a0cd3a;
    color: #fff; }

hr {
  background-color: #fff;
  margin: .5rem 0 2rem;
  width: 4rem;
  height: 0.3rem;
  border: 0; }

.contact {
  width: 144rem;
  font-family: 'Raleway', sans-serif;
  padding: 5rem 10rem 0; }
  .contact .form {
    background: url("../assets/images/news_bg.png") no-repeat; }
    .contact .form p {
      color: #fff; }
    .contact .form h2 {
      color: #fff; }
  .contact h1 {
    color: #fd8d5c;
    font-size: 4.5rem; }
  .contact h2 {
    font-size: 3.5rem;
    color: #1c75bb;
    margin-bottom: 2rem; }
  .contact p {
    font-size: 2rem;
    margin-top: 2rem;
    margin-bottom: 3rem; }
  .contact form ul {
    background-color: #1c75bb;
    list-style: none;
    padding: 1rem; }
    .contact form ul li {
      display: flex;
      justify-content: flex-end;
      align-items: center; }
      .contact form ul li label {
        flex: 1;
        font-size: 2rem;
        text-align: right;
        color: #fff;
        margin: 0.5rem; }
      .contact form ul li > input, .contact form ul li .input, .contact form ul li .input-select {
        flex: 2;
        background-color: #1c75bb;
        margin: 0.5rem;
        padding: 0.5rem; }
      .contact form ul li > input, .contact form ul li .input-select {
        background-color: #fff; }
