/* CSS Reset */
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  font-size: 62.5%; }

body {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  /*font-size: 16px;*/
  line-height: 1.7;
  box-sizing: border-box; }

img {
  display: block; }

.btn {
  font-family: 'Raleway', sans-serif;
  font-size: 1.4rem;
  font-weight: bolder; }
  .btn:link, .btn:visited {
    text-transform: uppercase;
    text-decoration: none;
    padding: 1.2rem 3rem;
    border-radius: 1.7rem; }
  .btn:hover {
    transform: translateY(-0.3rem); }
  .btn:active {
    transform: translateY(-0.1rem); }
  .btn--accent {
    background-color: #fd8d5c;
    color: #fff; }
  .btn--secondary {
    background-color: #a0cd3a;
    color: #fff; }

hr {
  background-color: #fff;
  margin: .5rem 0 2rem;
  width: 4rem;
  height: 0.3rem;
  border: 0; }

.footer {
  width: 144rem;
  height: 54rem;
  background-image: url("../assets/images/footer_bg.png");
  padding: 5rem 8rem;
  position: relative; }
  .footer > div {
    display: flex;
    justify-content: space-between;
    color: white; }
    .footer > div h1 {
      font-size: 3.5rem;
      font-family: 'Raleway', sans-serif; }
    .footer > div hr {
      background-color: #a0cd3a;
      margin: .5rem 0 2rem;
      width: 4rem;
      height: 0.3rem;
      border: 0; }
    .footer > div p {
      font-size: 1.8rem; }
    .footer > div input {
      background-color: rgba(255, 255, 255, 0.5);
      border: 0;
      margin-top: 2rem;
      padding: 1rem;
      width: 30rem;
      color: #fff;
      font-size: 1.8rem; }
    .footer > div .image-list {
      display: flex;
      align-items: center; }
      .footer > div .image-list img {
        margin-right: 0.75rem; }
    .footer > div .btn-container {
      margin-top: 4.5rem; }
  .footer p.copyright {
    color: #fff;
    position: absolute;
    bottom: 0;
    margin-bottom: 3rem; }
