/* CSS Reset */
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  font-size: 62.5%; }

body {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  /*font-size: 16px;*/
  line-height: 1.7;
  box-sizing: border-box; }

img {
  display: block; }

.btn {
  font-family: 'Raleway', sans-serif;
  font-size: 1.4rem;
  font-weight: bolder; }
  .btn:link, .btn:visited {
    text-transform: uppercase;
    text-decoration: none;
    padding: 1.2rem 3rem;
    border-radius: 1.7rem; }
  .btn:hover {
    transform: translateY(-0.3rem); }
  .btn:active {
    transform: translateY(-0.1rem); }
  .btn--accent {
    background-color: #fd8d5c;
    color: #fff; }
  .btn--secondary {
    background-color: #a0cd3a;
    color: #fff; }

hr {
  background-color: #fff;
  margin: .5rem 0 2rem;
  width: 4rem;
  height: 0.3rem;
  border: 0; }

.mission-row {
  width: 144rem;
  font-family: 'Raleway', sans-serif;
  display: flex;
  justify-content: flex-start;
  padding: 0 5rem; }
  .mission-row .side {
    flex: 1 0; }
  .mission-row .main {
    flex: 4 0;
    padding-left: 5rem; }
  .mission-row.top {
    padding-top: 9rem; }
    .mission-row.top > div {
      padding-top: 2rem; }
    .mission-row.top .side {
      display: flex;
      flex-direction: column;
      align-items: flex-end; }
      .mission-row.top .side .money {
        color: #a0cd3a;
        font-size: 6rem;
        font-weight: 900;
        text-align: right;
        margin: 2rem 0 1rem; }
      .mission-row.top .side .caption {
        color: #7d7d7d;
        font-size: 2.5rem;
        font-weight: 900;
        margin-bottom: 4rem;
        text-align: right;
        line-height: 2.5rem; }
      .mission-row.top .side hr {
        background-color: #f0f0f0;
        margin-bottom: 1rem;
        margin-left: auto;
        width: 12rem; }
    .mission-row.top .main {
      background: url("../assets/images/news_bg.png") no-repeat; }
      .mission-row.top .main p {
        color: #fff; }
      .mission-row.top .main h2 {
        color: #fff; }
  .mission-row h1 {
    color: #fd8d5c;
    font-size: 4.5rem; }
  .mission-row h2 {
    font-size: 3.5rem;
    color: #1c75bb;
    margin-bottom: 2rem; }
  .mission-row h3 {
    font-size: 3.5rem;
    color: #fd8d5c; }
  .mission-row h4 {
    font-size: 2rem; }
  .mission-row p {
    font-size: 2rem;
    margin-top: 3rem;
    margin-bottom: 6rem; }
  .mission-row > div {
    padding: 0 2rem; }
  .mission-row.board-member {
    padding-top: 0; }
    .mission-row.board-member .side img {
      width: 22rem;
      margin-top: 1rem; }
