/* CSS Reset */
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  font-size: 62.5%; }

body {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  /*font-size: 16px;*/
  line-height: 1.7;
  box-sizing: border-box; }

img {
  display: block; }

.btn {
  font-family: 'Raleway', sans-serif;
  font-size: 1.4rem;
  font-weight: bolder; }
  .btn:link, .btn:visited {
    text-transform: uppercase;
    text-decoration: none;
    padding: 1.2rem 3rem;
    border-radius: 1.7rem; }
  .btn:hover {
    transform: translateY(-0.3rem); }
  .btn:active {
    transform: translateY(-0.1rem); }
  .btn--accent {
    background-color: #fd8d5c;
    color: #fff; }
  .btn--secondary {
    background-color: #a0cd3a;
    color: #fff; }

hr {
  background-color: #fff;
  margin: .5rem 0 2rem;
  width: 4rem;
  height: 0.3rem;
  border: 0; }

.featured-pets {
  background-color: #f9ffed;
  padding: 5rem 8rem; }
  .featured-pets h1 {
    color: #1c75bb; }
  .featured-pets hr {
    background-color: #fd8d5c; }
  .featured-pets .pet-image {
    height: 54rem;
    width: 62rem; }
  .featured-pets > div {
    display: flex;
    justify-content: space-between; }
    .featured-pets > div > div {
      display: flex;
      flex-direction: column;
      align-items: center; }
  .featured-pets .btn-container {
    margin-top: 7rem; }
