/* CSS Reset */
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  font-size: 62.5%; }

body {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  /*font-size: 16px;*/
  line-height: 1.7;
  box-sizing: border-box; }

img {
  display: block; }

.btn {
  font-family: 'Raleway', sans-serif;
  font-size: 1.4rem;
  font-weight: bolder; }
  .btn:link, .btn:visited {
    text-transform: uppercase;
    text-decoration: none;
    padding: 1.2rem 3rem;
    border-radius: 1.7rem; }
  .btn:hover {
    transform: translateY(-0.3rem); }
  .btn:active {
    transform: translateY(-0.1rem); }
  .btn--accent {
    background-color: #fd8d5c;
    color: #fff; }
  .btn--secondary {
    background-color: #a0cd3a;
    color: #fff; }

hr {
  background-color: #fff;
  margin: .5rem 0 2rem;
  width: 4rem;
  height: 0.3rem;
  border: 0; }

.home-sponsors {
  width: 144rem;
  background-color: #fff;
  display: flex;
  justify-content: flex-start;
  padding-left: 8rem;
  padding-right: 8rem; }
  .home-sponsors > div {
    padding-top: 8rem;
    padding-bottom: 8rem; }
    .home-sponsors > div.tiles {
      padding: 0;
      position: relative;
      width: 60%;
      overflow: hidden;
      margin: 0 6rem 0; }
      .home-sponsors > div.tiles > div {
        position: absolute;
        width: 18rem;
        height: 23.7rem;
        font-size: 1rem;
        transition: all 1s;
        display: flex;
        flex-direction: column;
        justify-content: center; }
        .home-sponsors > div.tiles > div.hide {
          opacity: 0; }
        .home-sponsors > div.tiles > div img {
          width: 100%; }
  .home-sponsors h1 {
    font-size: 3.5rem;
    color: #1c75bb;
    margin-right: 6rem; }
  .home-sponsors hr {
    background-color: #fd8d5c; }
  .home-sponsors .arrow h1 {
    margin-right: 0; }
    .home-sponsors .arrow h1 a {
      color: #000;
      text-decoration-line: none; }
