/* CSS Reset */
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  font-size: 62.5%; }

body {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  /*font-size: 16px;*/
  line-height: 1.7;
  box-sizing: border-box; }

img {
  display: block; }

.btn {
  font-family: 'Raleway', sans-serif;
  font-size: 1.4rem;
  font-weight: bolder; }
  .btn:link, .btn:visited {
    text-transform: uppercase;
    text-decoration: none;
    padding: 1.2rem 3rem;
    border-radius: 1.7rem; }
  .btn:hover {
    transform: translateY(-0.3rem); }
  .btn:active {
    transform: translateY(-0.1rem); }
  .btn--accent {
    background-color: #fd8d5c;
    color: #fff; }
  .btn--secondary {
    background-color: #a0cd3a;
    color: #fff; }

hr {
  background-color: #fff;
  margin: .5rem 0 2rem;
  width: 4rem;
  height: 0.3rem;
  border: 0; }

.header {
  width: 144rem;
  height: 15rem;
  padding: 4rem 4% 0 4%;
  background-color: #1c75bb; }
  .header.home {
    background-color: #fff; }
  .header__logo {
    display: inline-block;
    padding-right: 1rem; }
    .header__logo img {
      height: 8.2rem; }
  .header__nav {
    /*float: right;*/
    display: inline-block;
    list-style: none; }
    .header__nav.home li a:link, .header__nav.home li a:visited {
      color: #1c75bb; }
    .header__nav li {
      display: inline-block;
      margin-left: 4rem; }
      .header__nav li a:link, .header__nav li a:visited {
        color: #fff;
        text-decoration: none;
        font-size: 1.7rem;
        font-weight: 700;
        border-bottom: 0.2rem solid transparent;
        transition: border-bottom 0.2s;
        padding-bottom: 0.3rem; }
      .header__nav li a:hover, .header__nav li a:active {
        border-bottom: 0.2rem solid #a0cd3a;
        padding-bottom: 0.3rem; }
  .header__btn--container {
    display: inline-block;
    margin-left: 8rem;
    position: relative; }
    .header__btn--container a {
      z-index: 200;
      position: relative; }
    .header__btn--container img {
      position: absolute;
      top: -105px;
      left: 64px;
      z-index: 100; }
